
.point-view-table-head {
  cursor: pointer;
}
.point-view-data-body-value {
  font-size: 12px;
}
.point-view-data-head-value {
  font-size: 12px;
  font-weight: bold;
}
.d3-tip {
  text-align: center;
  min-width: 50px;
  padding-top: 2px;
  padding-bottom: 2px;
  padding-left: 4px;
  padding-right: 4px;
  background-color: #3a3a3c;
  color: #fff;
  border: 1px solid #3a3a3c;
  border-radius: 5px;
  font-size: 12px;
}


